import React from 'react';
import { useSelector, useDispatch } from "react-redux";
import $ from 'jquery';
import sounds from '../audio/bard';

function Item() {
    const equipment = useSelector(state => state.equipment);
    const gold = useSelector(state => state.gold);
    const throwAway = useDispatch();

    const handlePlaySound = (id, x) => {
        const { item, potion, bookOpen, bookClose, gold, dagger, clothes } = sounds;
        switch(id) {
            case 'item':
                item.play();
                break;
            case 'gold':
                gold.play();
                break;
            case 'potion':
                potion.play();
                break;
            case 'book':
                if ($(`.item-desc.${x}`).hasClass('show') === true) {
                    bookClose.play();
                } else {
                    bookOpen.play();
                }
                break;
            case 'dagger':
                dagger.play();
                break;
            case 'clothes':
                clothes.play();
                break;
            default:
                item.play();
        }
    }

    const handleShowItemDesc = (x) => {
        if ($(`.item-desc.${x}`).hasClass('show')) {
            $(`.item-desc.${x}`).removeClass('show');
        } else {
            $(`.item-desc.${x}`).addClass('show');
        }
    }

    const handleThrowItemAway = (item) => {
        if (isNaN(item[2]) === true) {
            return window.alert('Nie możesz wyrzucić tego przedmiotu!')
        } else {
            const sure = window.confirm('Czy na pewno chcesz usunąć ten przedmiot z Twojego ekwipunku? Śmiecenie jest surowo zabronione dlatego przedmiot ulegnie zniszczeniu!');
            if (sure === true) {
                throwAway({ type: 'REMOVE_ITEM', data: item })
            }
        }
    }


        let i = 0;
        return (
            <div className="content-equipment_item">
                <div className="gold">
                    <h3>Dostępne złoto: {gold} sz</h3>
                </div>
                {equipment.map((m) => {let x = i; i++;
                    return <div key={m[0]} className="item">
                        <div className="item-name">
                            <h2 onClick={() => {handlePlaySound(m[4], x); handleShowItemDesc(x)}}>{m[0]}</h2>
                        </div>
                        <div className={"item-desc " + x}>
                            <div className="item-desc_collapse">
                                <p><b>Waga:</b> {m[1]}</p>
                                <p><b>Wartość:</b> {m[2]}</p>
                                <p><b>Ilość:</b> {m[5]}</p>
                                <p><b>Opis:</b> {m[3]}</p>
                                <button className="button-default" onClick={() => handlePlaySound(m[4])}>Użyj</button>
                                <button className="button-default" onClick={() => {sounds.click.play();handleThrowItemAway(m);}}>Wyrzuć</button>
                            </div>
                        </div>
                    </div>
                })}
            </div>
        );
    
}
 
export default Item;