export default function quests(state=[["Wyższe Tajemnice", "Dotrzyj do miasta Luskan do Wieży Tajemnych Arkan.", 20]], action) {
    switch(action.type) {
        case 'ADD_QUEST':
            return state.concat([action.data]);
        case 'REMOVE_QUEST':
            return state.filter(f => f !== action.data);
        default:
            return state;
    }
}