import * as items from '../../data/items.json';

export default function equipment(state=[
    items.QuestItems.IsmenaBook,
    items.Items.Bag,
    items.Items.FoodRations,
    items.Items.HealthPotions,
    items.OneHanded.Dagger,
    items.Items.adventureClothes
], action) {
    switch(action.type) {
        case 'ADD_ITEM':
            return state.concat([action.data]);
        case 'REMOVE_ITEM':
            return state.filter(f => f !== action.data);
        default:
            return state;
    }
}