import { combineReducers } from 'redux';
import effects from './effects';
import equipment from './equipment';
import gold from './gold';
import exp from './exp';
import hp from './hp';
import info from './info';
import quests from './quests';

export default combineReducers({
    effects,
    equipment,
    gold,
    exp,
    hp, 
    info,
    quests
})