export default function info(state=[], action) {
    switch(action.type) {
        case 'NEW_INFO_QUEST':
            return state = ['quest', action.data];
        case 'NEW_INFO_EFFECT':
            return state = ['effect', action.data];
        case 'NEW_INFO_ITEM':
            return state = ['item', action.data];
        default:
            return state;
    }
}